          <h2>الرقم المرجعي للتقرير</h2>
          <h3>رقم الهاتف:</h3>
          <h3>المملكة العربية السعودية،</h3>
        </div>
        <div>
          <h2>تاريخ إصدار التقرير</h2>
          <h3>رمز إيداع التقرير:</h3>
        </div> -->
        <div class="image-facilty">
          <img :src="data.facility.logo">
        </div>
      </div>
    </div>
    <div class="divider" />
