                </v-icon>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody class="has-fields">
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            إسم العميل
          </td>
          <td>{{ data.customer.cs_name || '' }}</td>
          <td
