            :style="styleSubData"
          >
            إسم مستخدم التقرير
          </td>
          <td>{{ data.trans_report_name || '' }}</td>
          <td
            class="field"
            :style="styleSubData"
          >
            الغرض من التقييم
          </td>
          <td>{{ data.evaluationpurpose.name || '' }}</td>
        </tr>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            أساس القيمة
          </td>
          <td>محمد أحمد</td>
          <td
            class="field"
            :style="styleSubData"
          >
            تاريخ التقييم
          </td>
          <td>{{ data.trans_evaluation_date || '' }}</td>
          <td
            class="field"
            :style="styleSubData"
          >
            تاريخ المعاينة
          </td>
          <td>{{ data.trans_inspection_date || '' }}</td>
        </tr>
        <tr>
