    <!-- الأصل محل التقييم -->
    <table class="table-fixed">
      <thead :style="styleData">
        <tr>
          <th colspan="5">
            <div class="header">
              <div class="header-text">
                الأصل محل التقييم
              </div>
              <div class="header-icon">
                <v-icon>
                  far fa-file-pdf
                </v-icon>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody class="has-fields">
        <tr>
          <td
            :style="styleSubData"
            class="field"
