    <!--بيانات المبنى-->
    <table class="table-fixed">
      <thead :style="styleData">
        <tr>
          <th colspan="6">
            <div class="header">
              <div class="header-text">
                بيانات المبنى
              </div>
              <div class="header-icon">
                <v-icon>
                  far fa-file-pdf
                </v-icon>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            مساحة الأرض
          </td>
          <td>{{ data.land_area || '' }}</td>

          <td
            class="field"
            :style="styleSubData"
          >
            المسطحات المبنية
          </td>
          <td>{{ data.additional_component_4 || '' }}</td>

          <td
            class="field"
            :style="styleSubData"
          >
            عدد الأدوار
          </td>
          <td>{{ data.prop_floor || '' }}</td>
        </tr>
      </tbody>
    </table>
    <!--بيانات الانشاء-->
    <table class="table-fixed">
      <thead :style="styleData">
        <tr>
          <th colspan="6">
            <div class="header">
              <div class="header-text">
                بيانات الانشاء
              </div>
              <div class="header-icon">
                <v-icon>
                  far fa-file-pdf
                </v-icon>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            تاريخ بداية الانشاء
          </td>
          <td>{{ data.additional_component_3 }}</td>

          <td
            class="field"
            :style="styleSubData"
          >
            المرحلة الرئيسية
          </td>
          <td>{{ data.additional_component_1 || '' }}</td>

          <td
            class="field"
            :style="styleSubData"
          >
            المرحلة الفرعية
          </td>
          <td>{{ data.additional_component_2 || '' }}</td>
        </tr>
      </tbody>
    </table>
    <div
      class="html2pdf__page-break"
      :data-number="['1','من',totalPages].join(' ')"
    />
    <table class="table-fixed first">
      <thead :style="styleData">
        <tr>
          <th colspan="6">
            <div class="header">
              <div class="header-text">
                تقييم الإنجاز
              </div>
              <div class="header-icon">
                <v-icon>
                  far fa-file-pdf
                </v-icon>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            البيان
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            تفاصيل المرحلة
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            النسبة الى التكلفة
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            التكلفة
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            نسبة الإنجاز
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            قيمة الإنجاز
          </td>
        </tr>
        <tr
          v-for="(p,index) in data.achievement.stages"
          :key="p.id"
        >
          <td>
            م{{ index + 1 }}
          </td>
          <td>
            {{ p.details }}
          </td>
          <td>
            {{ p.pc_to_cost }}
          </td>
          <td>
            {{ p.cost }}
          </td>
          <td>
            {{ p.pc_of_completion }}
          </td>
          <td>
            {{ p.achievement_value }}
          </td>
        </tr>
      </tbody>
    </table>

    <table class="table-fixed">
      <thead :style="styleData">
        <tr>
          <th colspan="6">
            <div class="header">
              <div class="header-text">
                النتائج
              </div>
              <div class="header-icon">
                <v-icon>
                  far fa-file-pdf
                </v-icon>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            قيمة العقار الحالية
          </td>
          <td>
            {{ data.achievement.prop_current_price }}
          </td>
        </tr>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            المبلغ المتبقي لاتمام البناء
          </td>
          <td />
        </tr>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            مدة الإنجاز المتبقية
          </td>
          <td>
            {{ data.additional_component_value_1 || '' }}
          </td>
        </tr>
      </tbody>
    </table>
    <!-- وصف العقار-->
    <table>
      <thead class="diff">
        <tr>
          <th colspan="4">
