            :style="styleSubData"
          >
            الجهة
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            طول ضلع الأرض
          </td>
          <td
            class="field"
            :style="styleSubData"
          >
            الوصف
          </td>
          <td
            class="field"
            :style="styleSubData"
