            شمالي
          </td>
          <td>{{ data.tall_northern || '' }}</td>
          <td>{{ data.northern_border || '' }}</td>
          <td>{{ data.north_facade || '' }}</td>
          <!-- <td>مساحة الأرض</td>
          <td>{{ data.land_area || '' }}</td> -->
        </tr>
        <tr>
          <td
            class="field"
            :style="styleSubData"
          >
            غربي
          </td>
          <td>{{ data.tall_western || '' }}</td>
          <td>{{ data.western_border || '' }}</td>
          <td>{{ data.western_facade || '' }}</td>
